/* 左侧菜单 */
.leftMenu{
  position: absolute;
  top: 60px;
  left: 0;
  bottom: 0;
  width: 200px;
  background: #fff;
}
/*内容区*/
.content{
  position: absolute;
  top: 60px;
  left: 200px;
  bottom: 0;
  right: 0;
  background: #ebeef3;
  padding: 20px;
}
/* 信号设备维护 */
.main .searchBox{
  min-height: 60px;
  background: #fff;
  border-radius: 5px;
  overflow: auto;
}
.main .searchBox li{
  width: 25%;
  min-height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main .searchBox .searchList{
  float: left;
  padding-left: 10px;
}
.main .searchBox .searchList label{
  color: #323232;
  padding-right: 5px;
}
.main .searchBox .searchList input{
  max-width: 180px;
}
.main .searchBox .searchBtn{
  float: right;
}
.main .btnBox{
  margin-top: 10px;
}
.main .gridBox{
  margin-top: 10px;
  background: #fff;
  padding: 5px;
  border-radius: 5px;
}
/* 设备 */
.infoBox{
  width: 100%;
  min-height: 100px;
  border: 1px solid #eee;
  border-radius: 5px;
  position: relative;
  margin-bottom: 20px;
}
.infoBox .infoBoxTitle{
  background: #fff;
  width: auto;
  position: absolute;
  top: -14px;
  left: 20px;
  padding: 3px 6px;
  height: 24px;
}
/* antd样式重置 */
.ant-table-thead > tr > th, .ant-table-tbody > tr > td{
